.valor_i {
    position: absolute;
    margin: 6.5% 6.5%;
    padding: 5px;
    border-radius: 10px;
    color: white;
    background-color: #225143d7;
}

.valor_a {
    position: absolute;
    margin: 6.5% 9%;
    padding: 5px;
    border-radius: 10px;
    color: white;
    background-color: #225143d7;
}

.valor_e {
    position: absolute;
    margin: 6.5% 7%;
    padding: 5px;
