/* Este es el que nos alinea el contenido en la misma linea */
.container {
    margin: auto;
    overflow: hidden;
    max-width: 1500px;
    max-width: var(--max-width-container); 
    padding: 20px 0; 
    padding: var(--padding-container); 
    overflow: auto;
}


/* Individual item */
.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

/* Change color on hover */
.bm-item:hover {
  color: white;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: rgb(59, 80, 74);
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background:rgb(59, 80, 74);
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill:rgb(59, 80, 74);
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgb(59, 80, 74);
}

.scroll {
  overflow-x: hidden; 
  overflow-y: hidden; 
}

.usuario {
  color: #e9dfaa;
}
